import React from 'react';
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader';
import cssVar from 'views/mobileView/styles';
import Tabs from 'views/mobileView/components/atoms/Tabs';
import AdminServices from 'services/api/AdminServices';
import db from 'libs/db';
import { formatDate } from 'views/mobileView/components/atoms/DateFormatter';
import { toast } from "react-toastify";
import { BsChevronLeft, BsChevronRight } from 'react-icons/bs';
import MintboxIcon from 'views/mobileView/components/atoms/MintboxIcon';
import ActivityLoaderMintbox from 'views/mobileView/components/atoms/ActivityLoaderMintbox';
import AMCIcon from 'views/mobileView/components/atoms/AMCIcon';
import RedGreenText from 'views/mobileView/components/atoms/RedGreenText';
import { baseImgURL } from 'assets/Constant';

function WatchlistScreen(props) {
  const [user, setUser] = React.useState()
  const [watchlist, setWatchlist] = React.useState([])
  const [loading, setLoading] = React.useState()
  React.useEffect(() => {
    loadData();
  }, [])

  const loadData = () => {
    try {
      db.get(db.model.user).then((p) => {
        setUser(p.userData)
        getWatchlist(p.userData.id)
      });
    } catch (error) {
      console.log(error)
    }
  }

  const getWatchlist = async (id) => {
    try {
      setLoading(true);
      db.get(db.model.watchlist).then(async (p) => {
        console.log(p)
        if (p === null) {
          let res = await AdminServices.getWatchlist(id).then(res => res);
          if (res.type) {
            await db.set(db.model.watchlist, JSON.stringify(res.data))
            setWatchlist(res.data)
            setLoading(false)
          }
        } else {
          setWatchlist(p)
          setLoading(false)
        }
      });
    } catch (error) {
      toast.error(error);
      setLoading(false)
    }
  }

  const MintboxView = () => {
    const mintboxArray = watchlist.filter(f => f.FundType === 3)
    return (
      <>
        {mintboxArray.length === 0 ?
          <div className='containerData mb-4'>
            <div style={{ padding: "4%", margin: "4% 10%", textAlign: "center" }}>
              <img src={baseImgURL+"SVG12.svg"} width="50%" style={{ margin: "4% auto" }} />
              <div style={{ fontSize: "18px", fontWeight: "500", margin: "4% 0%" }}>You are not tracking any mintboxes yet.</div>
              <button onClick={() => props.history.push({ pathname: '/list', state: { type: 3, query: "All" } })} className='solidButton'>Track more mintboxes</button>
            </div>
          </div>
          :
          <div>
            <div style={{ display: "flex", padding: "4% 4% 0 4%" }}>
              <div className='header1dark' style={{ flex: 1 }}>Watchlisted mintboxes</div>
            </div>
            {
              mintboxArray.map(mintbox => {
                return (
                  <div key={mintbox.FundCode} style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }} onClick={() => { props.history.push("/mintbox-detail/" + mintbox.FundCode) }}>
                    <div style={{ display: "flex", gap: "4%" }}>
                      <MintboxIcon risk={mintbox.RiskLevel} height="40px" width="40px" />
                      <div style={{ flex: "1" }}>
                        <div className='header1dark'>{mintbox.FundName}</div>
                        <div className='smallTextLight' style={{ marginTop: "1%" }}>Watchlisted on {formatDate(mintbox.CreatedOn)}
                        </div>
                      </div>
                      <div>
                        <div className='smallTextLight' style={{ marginBottom: '2%' }}>5Y CAGR</div>
                        <div className='header1dark'>
                          <RedGreenText text={parseFloat(mintbox.FiveYrReturn || 0).toFixed(2) + '%'} value={mintbox.FiveYrReturn || 0} />
                        </div>
                      </div>
                    </div>
                  </div>
                )
              })
            }
            <div className='footer'>
              <button onClick={() => props.history.push({ pathname: '/list', state: { type: 3, query: "All" } })} className='outlineButton'>Track more mintboxes</button>
            </div>
          </div>
        }
      </>
    )
  }
  const ETFView = () => {
    const etfArray = watchlist.filter(f => f.FundType === 1)
    const [duration, setDuration] = React.useState(0); // 0-> 1yr, 1-> 3yr, 2-> 5yr
    return (
      <>
        {etfArray.length === 0 ?
          <div className='containerData mb-4'>
            <div style={{ padding: "4%", margin: "4% 10%", textAlign: "center" }}>
              <img src={baseImgURL+"SVG12.svg"} width="50%" style={{ margin: "4% auto" }} />
              <div style={{ fontSize: "18px", fontWeight: "500", margin: "4% 0%" }}>You are not tracking any ETFs yet.</div>
              <button onClick={() => props.history.push({ pathname: '/list', state: { type: 1, query: "All" } })} className='solidButton'>Track more ETFs</button>
            </div>
          </div>
          :
          <div>
            <div style={{ display: "flex", padding: "4% 4% 0 4%" }}>
              <div className='header1dark' style={{ flex: 1 }}>Watchlisted ETFs</div>
              <button onClick={() => { setDuration((duration + 1) % 3) }} className='greenChipHeader' style={{ minWidth: '120px' }}>
                <BsChevronLeft style={{ paddingRight: "5px" }} size={14} />
                {
                  duration === 2 ? "5 year return" : duration === 1 ? "3 year return" : "1 year return"
                }
                <BsChevronRight style={{ paddingLeft: "5px" }} size={14} />
              </button>
            </div>
            {
              etfArray.map((fund, i) => {
                return (
                  <div key={i} style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }} onClick={() => props.history.push("/ETF-detail/" + fund.FundCode)}>
                    <div style={{ display: 'flex', gap: '4%' }}>
                      <AMCIcon amcName={fund.AMCName} height='24px' width='24px' />
                      <div style={{ flex: 1 }}>
                        <div className="header2dark lineClamp">{fund.FundName}</div>
                        <div className='smallTextLight' style={{ marginTop: '1%' }}>Watchlisted on {formatDate(fund.CreatedOn)}</div>
                      </div>
                      <div className='header1dark'>
                        {
                          duration === 2 ?
                            <RedGreenText text={parseFloat(fund.FiveYrReturn || 0).toFixed(2) + '%'} value={fund.FiveYrReturn} />
                            :
                            duration === 1 ? <RedGreenText text={parseFloat(fund.ThreeYrReturn || 0).toFixed(2) + '%'} value={fund.ThreeYrReturn} />
                              :
                              <RedGreenText text={parseFloat(fund.OneYrReturn || 0).toFixed(2) + '%'} value={fund.OneYrReturn} />
                        }
                      </div>
                    </div>
                  </div>
                )
              })
            }
            <div className='footer'>
              <button onClick={() => props.history.push({ pathname: '/list', state: { type: 1, query: "All" } })} className='outlineButton'>Track more ETFs</button>
            </div>
          </div>
        }
      </>
    )
  }

  const MFView = () => {
    const mfArray = watchlist.filter(f => f.FundType === 2)
    const [duration, setDuration] = React.useState(0)  // 0-> 1yr, 1-> 3yr, 2-> 5yr
    return (
      <>
        {mfArray.length === 0 ?
          <div className='containerData mb-4'>
            <div style={{ padding: "4%", margin: "4% 10%", textAlign: "center" }}>
              <img src={baseImgURL+"SVG12.svg"} width="50%" style={{ margin: "4% auto" }} />
              <div style={{ fontSize: "18px", fontWeight: "500", margin: "4% 0%" }}>You are not tracking any mutual funds yet.</div>
              <button onClick={() => props.history.push({ pathname: '/list', state: { type: 2, query: "All" } })} className='solidButton'>Track more mutual funds</button>
            </div>
          </div>
          :
          <div>
            <div style={{ display: "flex", padding: "4% 4% 0 4%" }}>
              <div className='header1dark' style={{ flex: 1 }}>Watchlisted mutual funds</div>
              <button onClick={() => { setDuration((duration + 1) % 3) }} className='greenChipHeader' style={{ minWidth: '120px' }}>
                <BsChevronLeft style={{ paddingRight: "5px" }} size={14} />
                {
                  duration === 2 ? "5 year return" : duration === 1 ? "3 year return" : "1 year return"
                }
                <BsChevronRight style={{ paddingLeft: "5px" }} size={14} />
              </button>
            </div>
            {
              mfArray.map((fund, i) => {
                return (
                  <div key={i} style={{ padding: "4%", borderBottom: "1px solid " + cssVar.getPropertyValue('--light') }} onClick={() => props.history.push("/fund-detail/" + fund.FundCode)}>
                    <div style={{ display: "flex", gap: "4%" }}>
                      <AMCIcon amcName={fund.AMCName} height='24px' width='24px' />
                      <div style={{ flex: 1 }}>
                        <div className="header2dark lineClamp">{fund.FundName}</div>
                        <div className='smallTextLight' style={{ marginTop: '1%' }}>Watchlisted on {formatDate(fund.CreatedOn)}</div>
                      </div>
                      <div className='header1dark'>
                        {
                          duration === 2 ?
                            <RedGreenText text={parseFloat(fund.FiveYrReturn || 0).toFixed(2) + '%'} value={fund.FiveYrReturn} />
                            :
                            duration === 1 ? <RedGreenText text={parseFloat(fund.ThreeYrReturn || 0).toFixed(2) + '%'} value={fund.ThreeYrReturn} />
                              :
                              <RedGreenText text={parseFloat(fund.OneYrReturn || 0).toFixed(2) + '%'} value={fund.OneYrReturn} />
                        }
                      </div>
                    </div>
                  </div>
                )
              })
            }
            <div className='footer'>
              <button onClick={() => props.history.push({ pathname: '/list', state: { type: 2, query: "All" } })} className='outlineButton'>Track more mutual funds</button>
            </div>
          </div>
        }
      </>
    )
  }

  const tabsObject = [
    { key: 1, title: 'mintboxes', view: MintboxView() },
    { key: 2, title: 'ETFs', view: ETFView() },
    { key: 3, title: 'Mutual funds', view: MFView() },
  ];
  return (
    <div className='mainContainer'>
      <MainHeader header="Watchlist" navigation={props.history} />
      {
        loading ?
          <ActivityLoaderMintbox />
          :
          <div className='dataContainer px-0'>
            <Tabs tabsObject={tabsObject} color={cssVar.getPropertyValue('--darkGreen')} />
          </div>
      }
    </div>
  )
}

export default WatchlistScreen